/* @group Open Sans Hebrew (Hebrew) Font Face */

/** Begin Open Sans Hebrew Regular */
@font-face {
  font-family: "Open Sans Hebrew";
  font-weight: 400;
  src: url("https://open-sans.com/fonts/OpenSansHebrew/OpenSansHebrew-Regular-webfont.eot?#iefix")
      format("embedded-opentype"),
    url("https://open-sans.com/fonts/OpenSansHebrew/OpenSansHebrew-Regular-webfont.woff")
      format("woff"),
    url("https://open-sans.com/fonts/OpenSansHebrew/OpenSansHebrew-Regular-webfont.ttf")
      format("truetype"),
    url("https://open-sans.com/fonts/OpenSansHebrew/OpenSansHebrew-Regular-webfont.svg#OpenSansHebrewRegular")
      format("svg");
  src: url("https://open-sans.com/fonts/OpenSansHebrew/OpenSansHebrew-Regular-webfont.eot");
}

/* End Open Sans Hebrew Regular */

